import React, { ButtonHTMLAttributes } from 'react';
interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  leftIconURL?: string
  rightIconURL?: string
  outline?: boolean
}

export function Button(props: ButtonProps) {
  return (
    <button 
      {...props}
      className={`flex items-center justify-center ${props.outline ? 'border border-[1.5px] border-[#13639E] text-[#13639E] py-[5px]' : 'text-white bg-[#13639E] py-[6px]'} rounded-[6px] px-[12px] gap-[4px] ${props.className || ''}`}>
      { props.leftIconURL && <img 
        className={`w-[15px] h-[15px] object-contain`}
        src={props.leftIconURL} /> }
      <div className={`text-[13px] font-bold flex-1`}>
        { props.children }
      </div>
      { props.rightIconURL && <img 
        className={`w-[15px] h-[15px] object-contain`}
        src={props.rightIconURL} /> }
    </button>
  );
}
